import React,{Suspense} from 'react';
import './home.css';
import Yes from './assets/yes.svg';
import No from './assets/no.svg';
import Pre from './assets/reload.svg';

class Header extends React.Component{
    render(){
        return (
            <div className="text-container">
                <h1 className="text">{this.props.text}</h1>
            </div>
        )
    }

}

class Preloader extends React.Component{
    constructor(props) {
        super(props);
        this.viewRef = React.createRef();
    }

    render(){
        return(
            <div className="preloader">
                <img className="preloader__image_animate" src={Pre}/>
            </div>
        );
    }
}

class Photo extends React.Component{
    render(){
        return (
            <div className="photo-container">
                <img className="photo" src={this.props.photo}/>
            </div>
        )
    }
}

class Button extends React.Component{
    render(){
        return (
            <div className="but" onClick={this.props.func}>
                {this.props.text}
            </div>
        )
    }
}

class Home extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
          page: "home",
          status: "yes",
          show: false,
          min: 0,
          max: 100,
          value: 0
        };
        this.handleMinChange = this.handleMinChange.bind(this);
        this.handleMaxChange = this.handleMaxChange.bind(this);
    }

    componentDidMount () {
        this.timeoutId = setTimeout(() => {
            this.setState({show: true});
        }, 3000);
      }

    componentWillUnmount () {
        if (this.timeoutId) {
            clearTimeout(this.timeoutId);
        }
    }

    getRandomArbitrary() {
        return Math.floor(Math.random() * (this.state.max - this.state.min) + this.state.min);
    }

    getRandomInt() {
        return (Math.floor(Math.random() * 2) === 0);
    }

    renderHeader(text) {
        return <Header text={text}/>
    }

    renderPhoto(photo) {
        return <Photo photo={photo}/>
    }

    renderYN(){
        if (!this.state.show)
            return (<Preloader/>)
        else
            if(this.state.status === "yes")
                return this.renderPhoto(Yes)
            else
                return this.renderPhoto(No)
    }

    changeStatus(){
        clearTimeout(this.timeoutId);
        this.setState({show: false});
        this.timeoutId = setTimeout(() => {
            this.setState({show: true});
        }, 3000);
        if (this.getRandomInt())
            this.setState({status: "yes"});
        else
            this.setState({status: "no"});
    }

    renderButton(text, func) {
        return <Button text={text} func={func}/>
    }

    changePage(page){
            this.setState({page: page})
    }

    handleMinChange(event) {
        this.setState({min: Math.floor(event.target.value)});
      }

    handleMaxChange(event) {
        this.setState({max: Math.floor(event.target.value)});
      }

    page() {
        if (this.state.page==="home"){
            return(
                <div>
                    {this.renderHeader("Выберите функцию")}
                    {this.renderButton("Да/Нет", ()=>{this.changePage("yesno")})}
                    {this.renderButton("Генератор чисел", ()=>{this.changePage("numgen")})}
                </div>
            )
        }
        else if (this.state.page==="yesno"){
            return(
                <div>
                    {this.renderYN()}
                    {this.renderButton("Генерировать", ()=>{if (this.state.show) this.changeStatus();})}
                    {this.renderButton("Назад", ()=>{if (this.state.show) this.changePage("home")})}
                </div>
            )
        }
        else if (this.state.page==="numgen"){
            return(
                <div>
                    <div className="number-container">
                        <h2 className="number"> {this.state.value} </h2>
                    </div>
                    <table className="in-container">
                        <tr>
                            <td>
                                <div className="label"> min: </div>
                            </td>
                            <td>
                                <div className="label"> max: </div>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <input className="in" type="number" value={this.state.min} onChange={this.handleMinChange}/>
                            </td>
                            <td>
                                <input className="in" type="number" value={this.state.max} onChange={this.handleMaxChange}/>
                            </td>
                        </tr>
                    </table>
                    {this.renderButton("Генерировать", ()=>{this.setState({value: this.getRandomArbitrary()})})}
                    {this.renderButton("Назад", ()=>{this.changePage("home", "Найди вторую половинку")})}
                </div>
            )
        }
    }
    render(){
        return (
            <div className="main-container"> 
                {this.page()}
            </div>
        )
    }
}

export default Home;